<template>
  <v-app>
    <v-app-bar
        fixed
        color="white"
        elevation="1"
    >

      <!--Back button-->
      <v-app-bar-nav-icon>
        <i class="fa fa-arrow-left fa-2x" style="color:#000000;" aria-hidden="true"></i>
      </v-app-bar-nav-icon>

      <!-- Page title -->
      <v-spacer></v-spacer>
      <v-toolbar-title id="Title">Edit Profile</v-toolbar-title>
      <v-spacer></v-spacer>

    </v-app-bar>
    
    <!--Page-->
    <v-main id="main">
      <v-container>
        <div class="content">
          <!--User Avatar-->
          <image-input @input="changeAvatar($event)">
            <div slot="activator">
              <v-avatar size="150px" v-ripple >
                <img :src="userData.avatar">
              </v-avatar>
            </div>
          </image-input>

          <br>
          <br>

          <v-slide-x-transition>
            <div v-if="saved == false">
              <v-btn class="primary" @click="uploadImage" :loading="saving">Save Avatar</v-btn>
            </div>
          </v-slide-x-transition>
          
          <form @submit.prevent="handleSubmit">
            <div class="field" :class="{ error: errors.has('name') }">
              <label>Username</label>
              <input type="text" name="username" v-model="username">
             
            </div>

            <div class="field" :class="{ error: errors.has('email') }">
              <label>Email</label>
              <input type="email" name="email" v-model="email" placeholder="Email">
            </div>

            <button class="ui button primary">Update profile</button>
        </form>

      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import ImageInput from '../../components/ImageInput';
import axios from 'axios'
export default {
  name: 'App',

  components: {
    ImageInput: ImageInput
  },

  data: () => ({
    userData: null,
    saving: false,
    saved: false,
    username: '',
    email: ''
  }),

  beforeCreate() {
    axios.get('/public/profile/getUserData').then(response => (this.userData = response["data"]))
    // axios.get('/public/profile/getTags').then(response => (this.tags = response["data"]))
    // axios.get('/public/profile/getBadges').then(response => (this.badges = response["data"]))
  },

  methods: {
    changeAvatar(array){
      this.userData.avatar = array.imageURL
    },

    uploadImage() {
      this.saving = true
      setTimeout(() => this.savedAvatar(), 1000)
    },

    savedAvatar() {
      this.saving = false
      this.saved = true
    },

    handleSubmit(){
        console.log('Name'.this.name);
        console.log('Gender'.this.gender);
    }
  }
};
</script>

<style>
@import '../styles.css';
#main {
  background-image: url(leaves.png);
  background-color: rgba(255, 255, 255, 0.4);
  background-blend-mode: lighten;
  background-repeat: repeat;
  background-position: center;
}

.content{
  margin-top: 8rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

</style>